
// @ts-nocheck
import { computed, reactive, toRefs, onMounted, watch } from "vue";
import { useStore } from "vuex";
import axios from "axios";

// @ts-ignore
import * as d3 from "d3";

// @ts-ignore
import $ from "jquery";

// @ts-ignore
import * as d3Sankey from 'd3-sankey'


import {
    InfoCircleTwoTone,
    DownOutlined,
    MonitorOutlined,
} from "@ant-design/icons-vue";
import { formatter } from "@/utils/helper";

export default {
    name: "SankeyDiagram",
    props: {
        isVisible: { type: Boolean },
        paper: { type: Object },
    },
    components: {
        InfoCircleTwoTone,
        DownOutlined,
        MonitorOutlined,
    },
    setup(props: any, context: any) {
        const store = useStore();
        const state = reactive({
            paperList: computed(() => store.state.paperList),
            filterPapers: computed(() => store.getters.filterPapers),
        });

        const init = function() { 
            let data = data_processing(state.filterPapers);
            draw_main(data);
        }

        const data_processing = function(data:Array<PaperList>) {
            const links = {};
            data.forEach(d => {
                const applications = d['applications']
                const tasks = d['tasks'] 
                const techniques = d['techniques']
                const applicationsL = applications.length;
                const tasksL = tasks.length;
                const techniquesL = techniques.length;

                if (applications.length > 0) {
                    let unit = ((1/applicationsL)/tasksL); 
                    // unit = 1;
                    applications.forEach((s:string) => {
                        tasks.forEach((t:string) => {
                            if (!(s in links))
                                links[s] = {}
                            if (!(t in links[s]))
                                links[s][t] = 0
                            links[s][t] += unit;                       
                        })
                    })

                    tasks.forEach((t: string) => {
                        let unit = ((1/tasksL)/techniquesL); 
                        // unit = 1;
                        techniques.forEach((c: string) => {
                            if (!(t in links))
                                links[t] = {}
                            if (!(c in links[t]))
                                links[t][c] = 0
                            links[t][c] += unit;
                        })
                    })
                }
            })

            let data_links = [];
            for (let s in links) {
                for (let t in links[s]) {
                    let s_name = s
                    let t_name = t
                    if (s == 'Object Detection')
                        s_name = 'OD'
                    else if (s == 'Dimensionality Reduction')
                        s_name = 'DR'
                    if (t == 'Object Detection')
                        t_name = 'OD'
                    else if (t == 'Dimensionality Reduction')
                        t_name = 'DR'

                    data_links.push({
                        'source': s_name,
                        'target': t_name,
                        'value': links[s][t]
                    })
                }
            }

            return data_links;
        }
     
        const draw_main = function (data:Array<object>) {
            let _width = $("#sankey-diagram").width();
            let _height = $("#sankey-diagram").height();
            let width = 1 * _width;
            let height = 1 * _height;

            let padding = {'left': 0.01*width, 'bottom': 0.01*height, 'top': 0.08*height, 'right': 0.01*width};
            let svg = d3.select('#sankey-diagram')
                .select('svg')
                .attr('width', width)
                .attr('height', height);
            
            svg.selectAll('*').remove();

            if (data.length == 0) {
                return;
            }

            const nodes = Array.from(new Set(data.flatMap(l => [l.source, l.target])), name => ({name}));

            const links = data.map(d => ({
                source: nodes.findIndex(n => n.name === d.source),
                target: nodes.findIndex(n => n.name === d.target),
                value: d.value
            }));

            const graph = {
                nodes,
                links
            };

            // 
            // const width = 800;
            // const height = 200;

            // const svg = d3.select("body").append("svg")
            //     .attr("width", width)
            //     .attr("height", height);

            const nodeRank = {
                'Generation': 0,
                'Assessment': 1,
                'Exploration': 2,
                'Retrieval': 3,
                'Feature Extraction': 4,
                'Visualization Recommendation': 5,
                'Reverse Engineering': 6,
                'Translation': 7,
                'Classification': 8,
                'Regression': 9,
                'DR': 10,
                'OD': 11,
                'OCR': 12
            }

            const sankey = d3Sankey.sankey()
                .nodeWidth(15)
                .nodePadding(10)
                .extent([[padding.left, padding.top], [width - 1-padding.right, height - 6-padding.bottom]])
                .linkSort((a,b) => b.value - a.value)
                // .nodeSort((a, b) => nodeRank[b.name] - nodeRank[a.name]);

            const {nodes: sankeyNodes, links: sankeyLinks} = sankey({
                nodes: graph.nodes.map(d => Object.assign({}, d)),
                links: graph.links.map(d => Object.assign({}, d))
            });

            // node
            svg.append("g")
                .selectAll("rect")
                .data(sankeyNodes)
                .join("rect")
                .attr("x", d => d.x0)
                .attr("y", d => d.y0)
                .attr("height", d => d.y1 - d.y0)
                .attr("width", d => d.x1 - d.x0)
                .attr('fill', '#1f77b4')
                .attr('fill-opacity', 0.8)
            // link
            svg.append("g")
                .attr("fill", "none")
                .selectAll("g")
                .data(sankeyLinks)
                .join("path")
                .attr("d", d3Sankey.sankeyLinkHorizontal())
                .attr("stroke", 'rgb(211,211,211)')
                .attr("stroke-width", d => Math.max(1, d.width));

            // text
            svg.append("g")
                .attr("font-family", "sans-serif")
                .attr("font-size", `${14*7/96}rem`)
                .selectAll("text")
                .data(sankeyNodes)
                .join("text")
                .attr("x", d => d.x0 < width / 2 ? d.x1 + 6 : d.x0 - 6)
                .attr("y", d => (d.y1 + d.y0) / 2)
                .attr("dy", "0.35em")
                .attr("text-anchor", d => d.x0 < width / 2 ? "start" : "end")
                .text(d => d.name);
        }

        onMounted(init);

        watch(
            () => state.filterPapers,
            (newValue, oldValue) => {
                init();
            }
        )

        return {
            ...toRefs(state),
        };
    },
};
