import { createApp } from 'vue'
import App from './App.vue'
import store from './store'
import ant from "./utils/ant"
// import * as d3 from 'd3'
// import * as d3Sankey from 'd3-sankey'
// import $ from 'jquery'

createApp(App)
    .use(store)
    .use(ant)
    .mount('#app')

function fnResize() {
    // remAPPwidth
    const h0 = 1080
    const w0 = 1920
    const deviceH = document.documentElement.clientHeight
    const deviceW = document.documentElement.clientWidth
    const h1 = 940
    const fontSize = (deviceW / w0) * 18
    if ((fontSize / 18) * h1 < deviceH) {
        document.documentElement.style.fontSize = (deviceW / w0) * 18 + 'px'
    } else {
        document.documentElement.style.fontSize = (deviceH / h0) * 18 + 'px'
    }
}

fnResize()

window.onresize = () => {
    fnResize()
    location.reload()
}