
// @ts-nocheck
import { watch, computed, reactive, toRefs, onMounted } from "vue";
import { useStore } from "vuex";
import axios from "axios";

// @ts-ignore
import * as d3 from "d3";

// @ts-ignore
import $ from "jquery";

import {
    InfoCircleTwoTone,
    DownOutlined,
    MonitorOutlined,
} from "@ant-design/icons-vue";
import { formatter } from "@/utils/helper";

export default {
    name: "Scatterplot",
    props: {
        isVisible: { type: Boolean },
        paper: { type: Object },
    },
    components: {
        InfoCircleTwoTone,
        DownOutlined,
        MonitorOutlined,
    },
    setup(props: any, context: any) {
        const store = useStore();
        const state = reactive({
            paperList: computed(() => store.state.paperList),
            filterPapers: computed(() => store.getters.filterPapers),
        });

        const init = function() {
            let x_attr = 'year';
            let y_attr = 'size';   
            draw_main(true, state.filterPapers, x_attr, y_attr);
        }
        const get_min_max = function(data: Array<Object>, attr: string) {
            let min = 1e9;
            let max = 0;
            data.forEach(d => {
                let v = parseInt(d[attr]);
                if (v > max)
                    max = v;
                if (v < min)
                    min = v;
            });

            return [min, max];
        }
     
        const draw_main = function (initialize:boolean, data:Array<object>, x_attr:string, y_attr:string) {
            let svg = d3.select('#scatterplot')
                .select('svg')   
                .attr('cursor', 'crosshair');
         
                let _width = $("#scatterplot").width();
                let _height = $("#scatterplot").height()-40;
                let width = 1 * _width;
                let height = 1 * _height;
                let padding = {'left': 0.15*width, 'bottom': 0.15*height, 'top': 0.1*height, 'right': 0.1*width};

                svg.attr('width', width)
                    .attr('height', height);

                // title
                // svg.append('g')
                //     .attr('transform', `translate(${padding.left+(width-padding.left-padding.right)/2}, ${padding.top*0.4})`)
                //     .append('text')
                //     .attr('class', 'title')
                //     .text('A Visualization for Faculties That Research on Computer Science in Well-known Universities');

                // x axis - phd graduation year
                let x = d3.scaleLinear()
                    // .domain(get_min_max(data, x_attr))
                    .domain([2010,2022])
                    .range([padding.left, width-padding.right]);    
                let axis_x = d3.axisBottom()
                    .scale(x)
                    .ticks(5)
                    .tickFormat(d => d);

                // y axis - publications
                let y = d3.scaleLog()
                    .domain([10, 1e8])
                    // .domain(get_min_max(data, y_attr))
                    .range([height-padding.bottom, padding.top]);    
                let axis_y = d3.axisLeft()
                    .scale(y)
                    // .ticks(10)
                    .tickValues([10, 100, 1000, 10000, 100000, 1000000, 10000000, 1e8])
                    .tickFormat(d3.format("~s"));

                    if (initialize) {
                // x axis
                svg.append('g')
                    .attr('transform', `translate(${0}, ${height-padding.bottom})`)
                    .call(axis_x)
                    // .attr('font-family', fontFamily)
                    .attr('font-size', '0.8rem')

                // svg.append('g')
                //     .attr('transform', `translate(${padding.left+(width-padding.left-padding.right)/2}, ${height-padding.bottom})`)
                //     .append('text')
                //     .attr('class', 'axis_label')
                //     .attr('dx', '-0.4rem')
                //     .attr('dy', 0.08*height)
                //     .text(x_attr);

                // y axis
                svg.append('g')        
                    .attr('transform', `translate(${padding.left}, ${0})`)
                    .call(axis_y)
                    // .attr('font-family', fontFamily)
                    .attr('font-size', '0.8rem')
                // svg.append('g')
                //     .attr('transform', `
                //         translate(${padding.left}, ${height/2})
                //         rotate(-90)    
                //     `)
                //     .append('text')
                //     .attr('class', 'axis_label')
                //     .attr('dy', -height*0.07)
                //     .text(y_attr);

                svg.append('g')
                    .attr('class', 'points')
            }

            // points
            let points = svg.select('g.points');
            points.selectAll('circle')
                .data(data, (d:any) => d.idx)
                .join(
                    enter => enter.append('circle')
                        .attr('class', 'point selected')
                        .attr('cx', (d, i) => {
                            return x(parseInt(d[x_attr]));
                        })
                        .attr('cy', (d, i:number) => y(parseInt(d[y_attr])))
                        .attr('r', 3)
                        .attr('fill', '#1f77b4')
                        .attr('fill-opacity', 0.7)
                        .append('title')
                        .text(d => d.idx),
                    update => update.attr('class', 'point selected')
                                .attr('fill', '#1f77b4')
                                .attr('fill-opacity', 0.7),
                    exit => exit.attr('class', 'point unselected')
                                .attr('fill', '#a4b1bb')
                                .attr('fill-opacity', 0.3),
                )

            if (initialize) {
                let box = svg.append('rect')
                    .attr('id', 'box')
                    .attr('width', 0)
                    .attr('height', 0)
                    .attr('x', 0)
                    .attr('y', 0)
                    .attr('opacity', 0.3)
                    .attr('cursor', 'move');
                let start = {x : 0, y : 0};

                let drag1 = d3.drag()
                    .on('start', function(e) {
                        start.x = e.sourceEvent.offsetX
                        start.y = e.sourceEvent.offsetY
                        box.attr('x', start.x)
                            .attr('y', start.y)
                            .attr('width', 0)
                            .attr('height', 0)
                            .attr('opacity', 0)
                    })
                    .on('drag', function(e) {
                        box.attr('x', d3.min([e.sourceEvent.offsetX, start.x]))
                            .attr('y', d3.min([e.sourceEvent.offsetY, start.y]))
                            .attr('width', Math.abs(e.sourceEvent.offsetX - start.x))
                            .attr('height', Math.abs(e.sourceEvent.offsetY - start.y))
                            .attr('opacity', 0.3)
                            .attr('fill', '#a4b1bb')
                            .style('visibility', 'visible')
                    })
                    .on('end', function(e) {
                        if (Math.abs(e.sourceEvent.offsetX - start.x) < 3 && Math.abs(e.sourceEvent.offsetY - start.y) <3) {
                            box.attr('x', 0)
                                .attr('y', 0)
                                .attr('width', 0)
                                .attr('height', 0)
                                .attr('opacity', 0)
                        }
                        box_filter(x_attr, y_attr, x, y, box);
                    })
                svg.call(drag1)

                let box_pos = {'x': 0, 'y': 0}
                let drag2 = d3.drag()
                    .on('start', function(e) {
                        start.x = e.sourceEvent.offsetX
                        start.y = e.sourceEvent.offsetY
                        box_pos.x = parseFloat(box.attr('x'))
                        box_pos.y = parseFloat(box.attr('y'))
                    })
                    .on('drag', function(e) {
                        let tx = box_pos.x + e.sourceEvent.offsetX - start.x,
                            ty = box_pos.y + e.sourceEvent.offsetY - start.y;
                        box.attr('x', tx)
                            .attr('y', ty)

                        box_filter(x_attr, y_attr, x, y, box);
                    })
                    .on('end', function() {
                    })
                box.call(drag2)
            }
        }

        const add_filter_1d = function(attr:string, sets:Array<number>) {
            if (attr == 'year') {
                attr = 'time';
                if (sets[0] == sets[1]) {
                    sets[0] = 2010;
                    sets[1] = 2022;
                }                
                sets[0] = Math.ceil(sets[0]);
                sets[1] = Math.floor(sets[1]);
                
            } else {
                if (sets[0] == sets[1]) {
                    sets[0] = 0;
                    sets[1] = 1e10;
                }
            }

            let filterPayload: FilterPayload = {
                    type: attr,
                    content: sets
                }
            store.commit('filter', filterPayload)
        }

        function box_filter(x_attr:string, y_attr:string, x:object, y:object, box:object) {
            let xmin, xmax, ymin, ymax;
            let box_x = parseFloat(box.attr('x')),
                box_y = parseFloat(box.attr('y')),
                box_w = parseFloat(box.attr('width')),
                box_h = parseFloat(box.attr('height'));
            xmin = x.invert(box_x);
            xmax = x.invert(box_x + box_w);
            ymin = y.invert(box_y + box_h);
            ymax = y.invert(box_y);

            add_filter_1d(x_attr, [xmin, xmax]);
            add_filter_1d(y_attr, [ymin, ymax]);
        }

        onMounted(init);

        watch(
            () => state.filterPapers,
            (newValue, oldValue) => {
                draw_main(false, newValue, 'year', 'size');
            }
        )

        return {
            ...toRefs(state),
        };
    },
};
